import "./App.css";
import ImageMapper from "react-img-mapper";
import bg from "./img/bg.png";
import useWindowDimensions from "./useWindowDimensions";
import { useState } from "react";
import { Document, Page, pdfjs } from "react-pdf";
import pdf1 from "./pdf/Oferta_Pielegniarki_i_Polozne.pdf";
import pdf2 from "./pdf/Oferta_Farmaceuci.pdf";
import Modal from "@mui/material/Modal";
import Box from "@mui/material/Box";
import CloseIcon from "@mui/icons-material/Close";
import DownloadIcon from "@mui/icons-material/Download";
import Stack from "@mui/material/Stack";
import Button from "@mui/material/Button";
import "react-pdf/dist/Page/AnnotationLayer.css";
import "react-pdf/dist/Page/TextLayer.css";
import ReactPlayer from "react-player/youtube";

pdfjs.GlobalWorkerOptions.workerSrc = `//unpkg.com/pdfjs-dist@${pdfjs.version}/build/pdf.worker.min.js`;

function App() {
  const { width, height } = useWindowDimensions();
  const MAP = {
    name: "stand",
    // GET JSON FROM BELOW URL AS AN EXAMPLE
    areas: [
      {
        title: "Akademia współczesnej pielęgniarki i położnej",
        shape: "poly",
        name: "1",
        fillColor: "#E8E1E04d",
        pdf: pdf1,
        coords: [91, 173, 100, 444, 287, 437, 282, 184],
      },
      {
        title: "Rola farmaceuty w procesie leczenia pacjenta",
        shape: "poly",
        name: "2",
        fillColor: "#E8E1E04d",
        pdf: pdf2,
        coords: [326, 183, 330, 435, 495, 429, 490, 190],
      },
      {
        title: "Spot 1",
        shape: "poly",
        name: "3",
        fillColor: "#E8E1E04d",
        youtube:
          "https://www.youtube.com/watch?v=Yk1Ej9aZ14I&ab_channel=EdukacjaiMedycyna",
        coords: [747, 182, 747, 328, 1015, 328, 1015, 182],
      },
      {
        title: "Spot 2",
        shape: "poly",
        name: "4",
        youtube:
          "https://www.youtube.com/watch?v=rOOrW3oLWyA&ab_channel=EdukacjaiMedycyna",
        fillColor: "#E8E1E04d",
        coords: [1066, 182, 1066, 328, 1339, 328, 1339, 182],
      },
      {
        title: "Roll up",
        shape: "poly",
        name: "5",
        fillColor: "#E8E1E04d",

        href: "https://www.edukacjaimedycyna.com/e-learning.html",
        coords: [1528, 332, 1522, 766, 1718, 796, 1723, 328],
      },
      {
        title: "WWW",
        shape: "circle",
        name: "6",
        fillColor: "#E8E1E04d",

        href: "https://www.edukacjaimedycyna.com",
        coords: [1153 + (1203 - 1153) / 2, 420 + (470 - 420) / 2, 20],
      },
      {
        title: "FB",
        shape: "circle",
        name: "7",
        fillColor: "#E8E1E04d",

        href: "https://www.facebook.com/edukacjaimedycyna",
        coords: [1226 + (1276 - 1226) / 2, 421 + (470 - 420) / 2, 21],
      },
      {
        title: "IG",
        shape: "poly",
        name: "8",
        fillColor: "#E8E1E04d",

        href: "https://www.instagram.com/edukacjaimedycyna",
        coords: [1306, 425, 1306, 468, 1346, 468, 1346, 425],
      },
      {
        title: "Stoisko wirtualne",
        shape: "poly",
        name: "9",
        fillColor: "#E8E1E04d",
        href: "https://brandoncontent.pl/wirtualne-stoisko/",
        coords: [507, 547, 514, 653, 629, 645, 616, 542],
      },
      {
        title: "Stoisko animacje",
        shape: "poly",
        name: "10",
        fillColor: "#E8E1E04d",
        href: "https://brandoncontent.pl/pobierz-portolio-i-oferte/",
        coords: [511, 683, 517, 794, 634, 783, 626, 674],
      },
    ],
  };

  const [pdf, setPdf] = useState(null);
  const [yt, setYt] = useState(null);
  const [pdfPages, setPdfPages] = useState(1);

  const onClick = (x) => {
    if (x.pdf != null) {
      setPdf(x.pdf);
    } else if (x.href != null) {
      window.open(x.href, "_blank", "rel=noopener noreferrer");
    } else if (x.youtube != null) {
      setYt(x.youtube);
    }
  };

  const onDocumentLoadSuccess = (x) => {
    setPdfPages(x.numPages);
  };

  const onClosePdf = () => {
    setPdf(null);
    setPdfPages(1);
  };
  const onCloseYt = () => {
    setYt(null);
  };

  return (
    <div className="App">
      <main className="Stand">
        <Modal
          open={yt != null}
          onClose={onCloseYt}
          sx={{
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
          }}
        >
          <Stack
            display="flex"
            spacing={0}
            alignItems="center"
            justifyContent="center"
            mt={0.5}
          >
            <Stack
              maxWidth={"600px"}
              width={"100%"}
              direction="row"
              justifyContent="flex-end"
              spacing={2}
            >
              <Button
                size="medium"
                color="error"
                variant="outlined"
                onClick={onCloseYt}
                sx={{ borderRadius: 8, color: "white" }}
              >
                <CloseIcon />
              </Button>
            </Stack>
            <ReactPlayer url={yt} width={width < 800 ? width : 800} />
          </Stack>
        </Modal>
        <Modal open={pdf != null} onClose={onClosePdf}>
          <Stack
            display="flex"
            spacing={3}
            alignItems="center"
            justifyContent="center"
            mt={0.5}
          >
            <Stack
              maxWidth={"600px"}
              width={"100%"}
              direction="row"
              justifyContent="flex-end"
              spacing={2}
            >
              <a href={pdf} download={"PDF"} target="_blank" rel="noreferrer">
                <Button
                  size="medium"
                  color="error"
                  variant="outlined"
                  sx={{ borderRadius: 8, color: "white" }}
                >
                  <DownloadIcon />
                </Button>
              </a>
              <Button
                size="medium"
                color="error"
                variant="outlined"
                onClick={onClosePdf}
                sx={{ borderRadius: 8, color: "white" }}
              >
                <CloseIcon />
              </Button>
            </Stack>

            <Box
              sx={{
                position: "absolute",
                top: "2%",
                overflowY: "auto",
                height: "100%",
                display: "block",
              }}
            >
              <Document file={pdf} onLoadSuccess={onDocumentLoadSuccess}>
                {[...Array(pdfPages)].map((x, i) => (
                  <Page scale={width > height ? 1 : 0.7} pageNumber={i + 1} />
                ))}
              </Document>
            </Box>
          </Stack>
        </Modal>
        <ImageMapper
          src={bg}
          map={MAP}
          responsive
          imgWidth={1776}
          strokeColor="#C0C0C0"
          onClick={onClick}
          parentWidth={width > height ? width : 1700}
        ></ImageMapper>
      </main>
    </div>
  );
}

export default App;
